html{
    font-size: 62.5%;
}

.model_container{
    width: 100%;
    border: 1px solid red;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal {
    position:fixed;
    top: 10%;
    left: 25%;
    right: 25%;
    bottom: 10%;
    color: #fff;
    background-color: #181818;
    width: 770px;
    margin: 20px 20px;
    margin-inline: auto;
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 2000;
    box-shadow: rgb(0 0 0 / 75%) 0px 3px 10px;
    transition: opacity 0.2s linear;
}

.modal .modal-header {
    position: relative;
  }
  
  .modal .modal-header::before {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    content: "";
    background: linear-gradient(0deg, #181818, transparent 50%);
  }
  
  .modal .modal-header video {
    border-radius: 6px 6px 0 0;
    display: block;
    width: 100%;
    opacity: 1;
  }

.modal-content {
    width: 500px;
    background-color: #fff;
}

.modal .modal-header .close {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    margin: 1em;
    z-index: 2;
    /* cursor: pointer; */
    /* background-color: #fff; */
    color: #fff;
    border-radius: 50%;
    height: 36px;
    padding: 8px;
    width: 36px;
    border: 0;
  }
  
  /* .modal .modal-header .close svg {
    outline: none;
  } */
  
  .modal .modal-header .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    width: 100%;
    bottom: 0;
    /* padding: 0 52px; */
    margin-bottom: 70px;
    z-index: 2;
  }
  
  .modal .modal-header .header-content .header-title {
    width: 100%;
  }

  .header-title{
    display: flex;
    flex-direction: column;
  }
  .modal .modal-header .header-content .header-title > img {
    width: 25%;
    margin-bottom: 1.5em;
  }
  
  .modal .modal-header .header-content .header-title .header-buttons {
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
  }
  
  .modal .modal-header .header-content .header-title .header-buttons button {
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding: 0.8rem; */
    /* cursor: pointer; */
    max-height: 42px;
    min-height: 32px;
    font-size: 1.6rem;
  }



  .modal-header > button{
    padding: 0 !important;
    background-color: black;
    border-radius: 50%;
    margin-right:20px !important;
  }

  .header-title{
    margin-inline: 40px !important;
  }
  
  .modal .modal-header .header-content .header-title .header-buttons .play,
  .modal .modal-header .header-content .header-title .header-buttons .add {
    margin-right: 10px;
  }
  
  .modal .modal-header .header-content .header-title .header-buttons .play {
    -webkit-appearance: none;
            appearance: none;
    border: 0;
    border-radius: 4px;
    padding-left: 2rem;
    padding-right: 2.4rem;
    background-color: #fff;
    color: #141414;
    font-weight: 700;
    line-height: 2.4rem;
  }
  
  .modal .modal-header .header-content .header-title .header-buttons .play svg {
    height: 100%;
    width: 100%;
    margin-right: 10px;
  }
  
  .modal .modal-header .header-content .header-title .header-buttons .volume,
  .modal .modal-header .header-content .header-title .header-buttons .add,
  .modal .modal-header .header-content .header-title .header-buttons .rate {
    max-width: 42px;
    min-width: 32px;
    background-color: rgba(42, 42, 42, 0.6);
    border: 1px solid rgba(255, 255, 255, 0.7);
    border-width: 2px;
    padding-left: 0.8rem;
    padding-right: 0.8rem;
    color: #fff;
    border-radius: 50%;
  }
  
  .modal
    .modal-header
    .header-content
    .header-title
    .header-buttons
    .volume:hover,
  .modal .modal-header .header-content .header-title .header-buttons .add:hover,
  .modal .modal-header .header-content .header-title .header-buttons .rate:hover {
    border-color: #fff;
    background-color: rgba(42, 42, 42, 1);
  }
  
  .modal .modal-header .header-content .header-title .header-buttons .volume {
    position: absolute;
    right: 0;
    opacity: 0.4;
    transition: opacity 0.2s ease-in-out;
  }
  
  .modal
    .modal-header
    .header-content
    .header-title
    .header-buttons
    .volume:hover {
    opacity: 1;
  }

.modal-header,
.modal-footer {
    padding: 10px;
}

.modal-title {
    margin: 0;
}

.content_desc{
    text-align: left !important;
}


.description-match > span{
    text-transform: capitalize
}



.modal .modal-body {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
    padding: 15px 20px;
  }
  
  .modal .modal-body .modal-content-first {
    padding-right: 40px;
    line-height: 25px;
  }
  
  .modal .modal-body .modal-content-first .modal-description {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
  }
  
  .modal .modal-body .modal-content-first .modal-description span,
  .modal .modal-body .modal-content-first .modal-description svg {
    margin-right: 10px;
  }
  
  .modal .modal-body .modal-content-first .modal-description span:first-child {
    color: #46d369;
  }
  
  .modal .modal-body .modal-content-first .modal-description span {
    font-size: 1.6rem;
    font-weight: 700;
  }
  
  .modal .modal-body .modal-content-first .modal-description svg {
    width: 30px;
  }
  
  .modal .modal-body .modal-content-first .modal-description .description-badge {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 15px;
    border: 1px solid hsla(0, 0%, 100%, 0.4);
    border-radius: 3px;
    color: hsla(0, 0%, 100%, 0.9);
    font-size: 1.2rem;
    padding: 0 0.5em;
    white-space: nowrap;
  }
  
  .modal .modal-body .modal-content-first p {
    font-size: 1.4rem;
    line-height: 24px;
    word-break: break-word;
  }
  
  .modal .modal-body .modal-content-second {
    word-break: break-word;
    display: flex !important;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
  
  .modal .modal-body .modal-content-second span {
    line-height: 20px;
  }
  
  .modal .modal-body .modal-content-second .title {
    color: #777;
  }
  
  .modal .modal-body .modal-content-second .cast,
  .modal .modal-body .modal-content-second .genres,
  .modal .modal-body .modal-content-second .show {
    margin-bottom: 10px;
    /* cursor: pointer; */
  }
  
  .modal .modal-body .modal-content-second .cast span:hover,
  .modal .modal-body .modal-content-second .genres span:hover,
  .modal .modal-body .modal-content-second .show span:hover {
    text-decoration: underline;
  }
  
  .modal .modal-body .modal-content-first,
  .modal .modal-body .modal-content-second {
    width: 100%;
    font-size: 1.6rem;
  }
  
  .hidden {
    display: none;
  }
  
  .visuallyhidden {
    opacity: 0;
  }

  .read_more{
    color: #51a3da;
    display: inline;
  }
.video-player-container {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%; /* 16:9 aspect ratio */
    overflow: hidden;
  }
  
  .react-player {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  .custom-controls {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 10px;
    background-color: #000;
    color: #fff;
  }
  
  input[type='range'] {
    width: 100px;
  }
  
